.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: rgba(138, 43, 6, 0.63);
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.div {
  display: flex;
  height: inherit;
}

.main-image {
  width: 100%;
  height: 40rem;
  z-index: 0;
  overflow: hidden;
  
  
}

.main-image img {
  width: 110%;
  height: 80%;
  object-fit: cover;
  transform: rotateZ(-3deg) translateY(-1rem) translateX(-5rem);
  position: fixed;
}